}

.skip {
    background-color: transparent;
    color: #CFCFCF;
    height: 40px;
    border: 1px solid #B1A5FA;
    border-radius: 20px;
    font-size: 18px;
    transition: all 0.5s;
    grid-row-start: 2;
    padding-left: 30px;
    padding-right: 30px;
    margin-bottom: 8px;
}

.skip:hover:enabled {
    cursor:pointer;
    background-color: #362F40;
    color: #FFFFFF
}

.skip:disabled {
    border-color: darkgrey;
    color: darkgrey;
    cursor:default;
}

.loader {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    background: linear-gradient(0deg, #675F99 33%, #AB9EFF 100%);
    box-sizing: border-box;
    -webkit-animation: rotation 1s linear infinite;
            animation: rotation 1s linear infinite;
  }
  .loader::after {
    content: '';  
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 44px;
    height: 44px;
    border-radius: 50%;
    background: #211D27;
  }
  @-webkit-keyframes rotation {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg)}
  }
  @keyframes rotation {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg)}
  } 

span {
    grid-column: span 3;
    grid-row-start: 1;
}

.skip-error-message {
    color: white;
    visibility: hidden;
    background: linear-gradient(267.35deg, #362F40 0%, rgba(54, 47, 64, 0.22) 102.71%);
    border-radius: 5px;
    width: 300px;
    height: 60px;
    line-height: 60px;
    opacity: 80%;
